import React, { useRef } from "react";
import { Layout, Row, Col, message, Form, Input } from "antd";
import "../App.css";
import "../css/login.css";
import AxiosApi from "../util/AxiosApi.js";

const Login = ({ isLogin, signupPage, openNotification }) => {
  const loginRef = useRef([]); //  id, pw
  const [msg, setMsg] = message.useMessage();

  // 
  const onLogin = async (option) => {
    const resultData = await AxiosApi("checkMember", "get", option);

    if (!resultData.stts) {
      if (resultData.data === "ERR_NETWORK") {
        openNotification();
      } else {
        msg.open({
          type: "error",
          content: ",  .",
          duration: 2,
        });
      }
    } else {
      isLogin();
    }
  };

  // 
  const onSignUp = () => {
    signupPage();
  };

  return (
    <Layout className="layout">
      <Row className="login-box">
        <Col className="img-area">
          <h1 className="title">iPhone 15</h1>
          <div>
            <p className="sub-title">2023-09-22()  </p>
            <p className="sub-title">USB-C  </p>
            <p className="sub-title">  </p>
            <p className="sub-title">
                    
            </p>
            <p className="sub-title">  </p>
          </div>
        </Col>
        <Col className="input-area-login">
          <Form className="login-form" name="loginForm" onFinish={onLogin}>
            <h1 className="title">Template</h1>
            {/* <label className="form-label"></label> */}
            <Form.Item
              name="id"
              rules={[
                {
                  required: true,
                  message: " .",
                },
              ]}
            >
              <Input
                className="form-input"
                placeholder=""
                ref={(el) => (loginRef.current[0] = el)}
              />
            </Form.Item>
            {/* <label className="form-label"></label> */}
            <Form.Item
              className="form-item"
              name="pw"
              rules={[
                {
                  required: true,
                  message: " .",
                },
              ]}
            >
              <Input
                type="password"
                className="form-input"
                placeholder=""
                ref={(el) => (loginRef.current[1] = el)}
              />
            </Form.Item>
            <Form.Item>
              <button type="submit" className="btn-fade btn-primary btn-full">
                
              </button>
              <a className="link link-primary link-full" onClick={onSignUp}></a>
            </Form.Item>
          </Form>
        </Col>
      </Row>
      {setMsg}
    </Layout>
  );
};
export { Login };
